.cb-container{
    width: 100%;
    min-height: 100vh;

    display: flex;
    align-items: center;
    justify-content: center;

    background: url(https://ik.imagekit.io/qljqw3tvn/ibb/rad.webp) fixed center no-repeat;
    background-size: cover;
    /* background-color: #90be6d; */
    font-size: 5rem;
    color: white;
    
}

.scroll {
    width: 60px;
    height: 60px;
    
    border: 5px solid black;
    background-color: black;
    border-radius: 50%;
    
    position: absolute;
    bottom:5vh;
    left: 50%;

    z-index: 6;
    cursor: pointer;
    animation: down 1.5s infinite;
    -webkit-animation: down 1.5s infinite;
}

.scroll:before {
      content: '';
      position: absolute;
      top: 10px;
      left: 13px;
      width: 18px;
      height: 18px;
      border-left: 5px solid white;
      border-bottom: 5px solid white;
      border-radius: 6px;
      transform: rotate(-45deg);
}


@keyframes down {
    0% {
      transform: translate(0);
    }
    20% {
      transform: translateY(15px);
    }
    40% {
      transform: translate(0);
    }
  }
  

